import React, { Component } from 'react';
import  { Button, Jumbotron } from 'react-bootstrap';

import * as api from '../services/api';

class About extends Component {
  render() {
    return (
        <div className='my-5'>
            <h2 className='text-center'>Conheça a Escola de Circo <strong>Além Da Lona</strong></h2>
            <hr/>
            <section className='flex-row'>
                { api.about.map((item) => {
                    return (
                        <Jumbotron className="jamboBackground text-white">
                        <div className="m-auto">
                            <h1><strong>{ item.title }</strong></h1>
                            <p>{ item.text[0] }</p>
                            <p>{ item.text [1] }</p>
                            <p className="d-flex justify-content-end">
                            <Button variant="warning text-uppercase">Inscreva-se</Button>
                            </p>
                        </div>
                      </Jumbotron>
                );
                })
                }
            </section>
        </div>
    );
  }
}

export default About;



