import React, { Component } from 'react';
import  Carousel from 'react-bootstrap/Carousel';

import * as api from '../services/api';

class CarouselSection extends Component {
  render() {
    return (
      <Carousel className="my-5 CarouselContainer">
        { api.carouselImage.map((item) => {
          return (
            <Carousel.Item>
              <img
                className='d-block w-100 img-default'
                src={ item.src }
                alt={ item.alt }
              />
              <Carousel.Caption>
                <h3><strong>{ item.title }</strong></h3>
                <p><strong>{ item.text }</strong></p>
                <button className="btn btn-warning text-uppercase">Inscreva-se</button>
              </Carousel.Caption>
            </Carousel.Item>
          );
        }) }
      </Carousel>
    );
  }
}

export default CarouselSection;



