import React, { Component } from 'react';
import { Card, Button} from 'react-bootstrap';

import * as api from '../services/api';

class Tutorials extends Component { 
  render() {
    return (
      <div>
        <h2 className='text-center'>Faça Você Mesmo !</h2>
        <hr/>
          <section class='row'>
          { api.tutotials.map((item) => {
              return (
                <Card className='mx-auto my-3 bg-light shadow' style={{ width: '18rem' }}>
                  <Card.Img variant="top" src={ item.src } alt={ item.alt } />
                  <Card.Body>
                    <Card.Title>{ item.title }</Card.Title>
                    <Card.Text>{ item.text }</Card.Text>
                    <Button variant="warning">Ver Mais</Button>
                  </Card.Body>
                </Card>
            );
          })
          }
         </section>
      </div>
    );
  }
}

export default Tutorials;



