import React, { Component } from 'react';
import { Navbar, Nav } from 'react-bootstrap';

class NavBar extends Component {
  render(){
    return(
        <Navbar className="bg-warning" expand="lg">
            <Navbar.Brand href="#home"><strong>Além Da Lona</strong></Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mx-auto">
                    <Nav.Link className="mx-4" href="#home">Home</Nav.Link>
                    <Nav.Link className="mx-4" href="#about">Sobre</Nav.Link>
                    <Nav.Link className="mx-4" href="#espec">Espetaculos</Nav.Link>
                    <Nav.Link className="mx-4" href="#school">Clipping</Nav.Link>
                    <Nav.Link className="mx-4" href="#school">Contato</Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    );
  }
}

export default NavBar;


