import React, { Component} from 'react';
import { Image } from 'react-bootstrap';
import * as api from '../services/api'; 


class Squad extends Component {
  render() {
    return (
      <section className='m-5'>
        <h1 className="text-center">Artistas e Instrutores</h1>
        <hr/>
        {api.employees.map((item) =>
        <section className="row my-5">
          <Image src={ item.src } alt={ item.alt } roundedCircle className="col-6 col-lg-3 col-md-3"/>
          <div className="col-lg-6 col-md-6">
            <h3>{ item.name }</h3>
            <p>{ item.office }</p>
            <p>{ item.resume }</p>
          </div>
        </section>
        )}
      </section>
    );
  }
}

export default Squad;


